<template>
  <div>
    <loading v-if="isLoading"></loading>
    <p class="categories_titel">商品分類</p>
    <div class="categories">
      <div class="items">
        <div class="item">
          <img src="../assets/img/products/1.png" alt="">
          <p class="item_name">精選商品</p>
        </div>
        <div class="item">
          <img src="../assets/img/products/2.png" alt="">
          <p class="item_name">精靈球</p>
        </div>
        <div class="item">
          <img src="../assets/img/products/8.png" alt="">
          <p class="item_name">藥品補給</p>
        </div>
        <div class="item">
          <img src="../assets/img/products/21.png" alt="">
          <p class="item_name">野外求生道具</p>
        </div>
        <div class="item">
          <img src="../assets/img/products/26.png" alt="">
          <p class="item_name">精選組合包</p>
        </div>
      </div>
    </div>
    <p class="categories_titel">精選商品</p>
    <div class="products_list">
      <!-- swiper -->
      <swiper :options="swiperOption">
        <swiper-slide v-for="product in products" :key="product.id">
          <div class="categories">{{ product.category }}</div>
          <img :src="product.imageUrl" alt="">
          <div class="title">{{ product.title }}</div>
          <p class="intro">{{ product.description }}</p>
          <div class="price">
            <div class="onsale_price">NT${{product.price}}元</div>
            <div class="ori_price">原價{{ product.origin_price }}元</div>
          </div>
          <div class="more">
            <div class="read_more">詳細內容</div>
            <div class="add_cart" @click="addtoCart(product.id,product.title)">加入購物車</div>
          </div>
        </swiper-slide>
        <div class="swiper-pagination" slot="pagination"></div>
      </swiper>
    </div>
  </div>
</template>

<script>

import $ from "jquery";
import "swiper/dist/css/swiper.css"; // require styles
import { swiper, swiperSlide } from "vue-awesome-swiper";


import loading from "@/components/loading";

export default {
  name: "products",
  data() {
    return {
      swiperOption: {
        slidesPerView: 4,
        spaceBetween: 10,
        speed: 600, 
        autoplay:{
          delay: 5000,
        },
        pagination: {
          el: '.swiper-pagination',
          clickable: true
        },
        breakpoints: {
          480: {
            slidesPerView: 1,
            spaceBetween: 20
          },
          640: {
            slidesPerView: 2,
            spaceBetween: 30
          },
          800: {
            slidesPerView: 3,
            spaceBetween: 30
          }
        }
      },
      isLoading: false,
      products: [],
      pagination:{},
      tempProduct: {},
      screenWidth: document.body.clientWidth
    }
  },
  methods:{
    getPorducts(page = 1) {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products?page=${page}`;
      vm.isLoading = true
      this.$http.get(api).then(function(response) {
        //console.log(response.data)
        vm.products = response.data.products;
        vm.pagination = response.data.pagination
        vm.isLoading = false
      });
    },
    addtoCart(id,title) {
      this.$emit('addtoCart', id,title);
    }
  },
  created(){
    this.getPorducts()
  },
  computed:{
    starProduct(){
      const vm = this
      return vm.products.filter((item)=>{
        return item.category === '2017'
      })
    }
  },
  components: {
    loading,
    swiper,
    swiperSlide
  }
};
</script>

<style lang="scss" scoped>
.swiper-container {
  max-width: 1080px;
  width: 100%;
  height: 340px;
}
.swiper-slide{
  border: 1px solid #ddd;
  position: relative;
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 5px 15px;
  box-sizing: border-box;
  .categories{
    background: #ff4c4c;
    font-size: 12px;
    display: inline-block;
    color: #fff;
    position: absolute;
    top: 0;
    margin: 0;
    right: 0;
    padding: 3px 5px;
    border-radius: 5px;
    border: 2px solid #fff;
  }
  img{
    max-width: 140px;
    max-height: 140px;
    padding: 10px;
  }
  .title{
    font-size: 18px;
    align-self: flex-start;
  }
  .intro{
    align-self: flex-start;
    font-weight: 400;
    color: #666;
    font-size: 14px;
    padding-bottom: 10px;
    border-bottom: 1px solid #ccc;
  }
  .price{
    font-weight: normal;
    width: 100%;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    .onsale_price{
      font-size: 16px;
      letter-spacing: 1px;
      color: #48a38e;
      font-weight: 600;
    }
    .ori_price{
      font-size: 12px;
      color: #888;
    }
  }
}
.categories_titel{
  position: relative;
  font-size: 20px;
  font-weight: 600;
  color: #53acac;
  padding: 10px;
}
.categories{
  margin: 10px 0;
  .items{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-around;
    .item{
      cursor: pointer;
      display: flex;
      flex:1;
      flex-direction: row;
      align-items: center;
      justify-content: center;
      padding: 10px;
      border-radius: 5px;
      border: 0.5px solid #cccccc;
      margin: 5px;
      transition: .3s all;
      &:hover{
        border: .5px solid rgb(0, 119, 199);
      }
      img{
        max-width: 50px;
        max-height: 50px;
      }
      .item_name{
        color: #333;
        margin-left: 10px;
      }
    }
  }
}
.more{
  width: 100%;
  display: flex;
  margin-top: 10px;
  justify-content: space-between;
  .read_more,.add_cart{
    flex:1;
    text-align: center;
    padding: 5px 0;
    border: 1px solid #ccc;
    cursor: pointer;
  }
  .read_more{
    margin-right: 5px;
    &:hover{
      background: #ffc238;
      border: 1px solid #ffc238;
    }
  }
  .add_cart{
    &:hover{
      background: #ff4c4c;
      border: 1px solid #ff4c4c;
      color: #fff;
    }
  }
}
.products_list{
  padding: 20px 0;
}
@media screen and (max-width: 640px) {
  .swiper-container{
    width: 95%;
  }
  .swiper-slide{
    .categories{
      font-size: 16px;
      margin: 6px;
    }
  }
  .categories{
    margin: 0;
    .items{
      .item{
        flex-direction: column;
        font-size: 12px;
      }
    }
  }
}
</style>



